  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.custom-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.custom-checkmark {
  position: absolute;
  top: 2px;
  left: 0;
  height: 20px;
  width: 20px;
  border: 1px solid #090446;
}
.custom-container input:checked ~ .custom-checkmark {
  background-color: #090446;
}
.custom-checkmark:after {
  content: "";
  position: absolute;
  display: none;
}
.custom-container input:checked ~ .custom-checkmark:after {
  display: block;
}
.custom-container .custom-checkmark:after {
  left: 6px;
  top: 2px;
  width: 6px;
  height: 11px;
  border: solid white;
  border-width: 0 2px 2px 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}
